import { Html } from '@fui/core';
import { htmlDecode, shortcut } from '../../core.service';
import './html.less';

@shortcut()
export class ReportHtml extends BI.Widget {
    public static xtype = 'report.components.html';

    private htmlContainer: Html;

    public props: {
        baseCls: string,
        text: string,
        textAlign: string,
        lineHeight: number | null,
        whiteSpace: string,
    } = {
        baseCls: 'report-html',
        text: '',
        textAlign:  'left',
        lineHeight: null,
        whiteSpace: 'normal',
    }

    public render () {
        const { text, ...args } = this.options;

        return {
            ...args,
            type: BI.Html.xtype,
            ref: (_ref: Html) => {
                this.htmlContainer = _ref;
            },
            text: htmlDecode(text),
        };
    }

    public setText (text: string) {
        this.htmlContainer.setText(htmlDecode(text));
    }
}
